<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process273"></a>

<p><br></p>
<p><span class="rvts17">越老越值钱（</span><span class="rvts50">Old Means Gold</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:181,Y:272</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：猫鉴定者（</span><span class="rvts20">Cat the Appraiser</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">找到林德</span><span class="rvts20"></span><span class="rvts21">凯姆藏宝室中的三件值钱宝贝，并交易给猫鉴定者</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1499.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">进入凯姆的洋馆，与厨房处打杂的猫鉴定者谈话，她会告诉你她来凯姆洋馆的目的是为了偷</span><span class="rvts20">3</span><span class="rvts21">件古董宝贝，一件古画，一个卷轴，一个酒杯。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1500.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">进入林德</span><span class="rvts20"></span><span class="rvts21">凯姆的后花园，会找到一处隐藏的闸刀。该闸刀需要一定的智力（</span><span class="rvts20">wits</span><span class="rvts21">）值才可以发现，另外该闸刀处于上锁状态，需要使用较高的偷盗技能开锁。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1501.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">拉下隐藏闸刀后，在后花园这个位置（</span><span class="rvts20">X:135,Y:285</span><span class="rvts21">）可以找到隐藏的地下室，这个地下室就是林德</span><span class="rvts20"></span><span class="rvts21">凯姆的藏宝室。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1502.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在藏宝室中，会遭遇</span><span class="rvts20">3</span><span class="rvts21">处石头守卫，杀掉它们后，它们会在</span><span class="rvts20">1</span><span class="rvts21">轮或</span><span class="rvts20">3</span><span class="rvts21">秒后立马复活。记得杀掉它们后，使用</span><span class="rvts20"></span><span class="rvts21">源力吸取</span><span class="rvts20"></span><span class="rvts21">，就可以避免它们的再次复活，而且动作一定要快。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1503.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在这里可以找到神圣油画。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1504.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在这里可以找到猎王酒杯。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1505.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在这里可以找到永恒卷轴。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1506.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">之后返回猫鉴定者那里，告诉她你已经找到了油画，酒杯，卷轴这三样宝贝。然后记得与她物物交易（</span><span class="rvts20">Barter</span><span class="rvts21">），而不是直接卖给她。这样可以成功关闭该支线。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



